import React, { useState } from "react";

function Szamlalo(props) {
  const [count, setCount] = useState(0);

  function novel() {
    setCount(count + props.n);
  }

  function csokkent() {
    setCount(count - props.n);
  }

  function nullaz() {
    setCount(0);
  }

  return (
    <div className="container">
      <h1 style={count > 10 ? { color: "red" } : { color: "white" }}>
        {count}
      </h1>
      <button onClick={csokkent}>-{props.n}</button>
      <button onClick={novel}>+{props.n}</button>
      <button onClick={nullaz}>0</button>
    </div>
  );
}

export default Szamlalo;
